import react, { useState,useEffect } from 'react';
import { useParams } from 'react-router';
const ArticlePage =()=>{
    const { name }=useParams();
    const[article, setArticle] = useState(null);

    useEffect(() => {
        console.log('use effect: init')
        getArticlebyName();
      }, []);


      const getArticlebyName = async () => {
        console.log('before getArticlebyName')  
        const response = await fetch(
          `http://localhost:8000/api/article/getArticlebyName/${name}`)
        const data = await response.json();
        setArticle(data[0])
            };

if(article==null) return <h1>Article Not Found!!!</h1>;
    return (<div><h1>This is article Page {article.title}</h1>  
   <div>
       {
           article.content.map((content,key)=>
           <div>{content}</div>
           )
       }
   </div>
   </div>);
};
export default ArticlePage;